
body {
  margin: 0;
  font-family: "Lato", sans-serif;
  box-sizing: border-box;
}
.sidebar{
    flex: 20%;
}
.sidebar h4{
    text-align: left;
    margin: 0px;
    padding: 10px 10px 0px 10px;
}
.sidebar p{
    margin: 0px;
    padding: 10px 10px 0px 10px;
}
.sidebar .line{
    border-top: 2px solid #eee;
    margin: 0 30px 10px 30px;
}
.sidebar .yearbtn{
    padding: 10px 16px;
    display: flex;
}
.sidebar .yearbtn ul{
    padding: 0px;
    margin: 0px;
}
.sidebar .yearbtn ul li{
    list-style: none;
    width: 50%;
    float: left;
    padding-top: 10px;
}
.sidebar .yearbtn ul li button{
    padding: 5px 16px;
    background: #87ff71e0;
    border-radius: 3px;
    outline: none;
    border: transparent;
    cursor: pointer;
}
.sidebar .yearbtn ul li button:hover{
    background:#24e700e0;
}
.sidebar .yearbtn ul li button:active{
    background:#24e700e0;
}
.mainbar{
    flex:80%;
}
.content{
    background-color: #f1f1f1;
    color: black;
}
.content h2{
    margin: 0px;
    padding: 10px 10px 0px 10px;
}


/* card */
.cardContainer{
    display: flex;
    flex-wrap: wrap;
    background-color: #f1f1f1;
    justify-content: space-between;
    margin: 0px;
    padding: 0px;
}
.card{
    background: #fff;
    flex-basis: 24%;
    margin-left: 5px;
    margin-right: 5px;
    margin-bottom: 15px;
    border-radius: 10px;
}
.cardimg{
    width: 100%;
}
.cardimg img{
    width: 90%;
    padding: 10px;background: #c8c8c8;margin-top: 14px;
}
.cardcont{
    text-align: left;
    color: #000;
    padding-left: 15px;
}
.cardheader{
  margin: 0px;
  padding: 0px 0px;
  color: #1010f8;
}
.missionId{
    margin: 0px;
    padding: 0px 0px;
}
.missionIdUl{
    padding-left: 16px;
    margin: 0px;
}


.wd{
  width: 100%;
}
.launchYear{
    margin: 0px;
    padding: 0px 0px;
}
.SuccessfulLaunch{
    margin: 0px;
    padding: 0px 0px;
}
.SuccessfulLanding{
    margin: 0px;
    padding: 0px 0px;
}
.bluetext{
    font-size: 12px;
    color: cornflowerblue;
}
.flex-container {
    display: flex;
    align-items: stretch;
    background-color: #f1f1f1;
  }
  
  .flex-container > div {
    background-color: #fff;
    margin: 10px;
    text-align: center;
  }


@media screen and (max-width: 700px) {
    .flex-container {
        flex-wrap: wrap;
      }
      .cardContainer {
        display: block;
    }
}

